import React, { useState } from 'react';
import { Button, Grid, CircularProgress, Typography } from '@material-ui/core';
import { ArrowForward } from '@material-ui/icons';
import { makeStyles } from '@material-ui/core/styles';
import { useMountEffect } from '../../../Utility/useMountEffect';
import { getStorage } from '../../../Utility/helpers';

const useStyles = makeStyles({
  checker: {
    flex: 2,
    '& button': {
      width: '200px',
      height: '50px',
      fontSize: '14px',
    },
  },
  updateContainer: {
    flex: 3,
    '& h3': {
      fontSize: '28px',
      textAlign: 'center',
    },
  },
});

const Update = ({ autoCheck, handleOnboarding }) => {
  const classes = useStyles();

  const [checking, setChecking] = useState(false);
  const [text, setText] = useState('Update device');
  const [updates, setUpdates] = useState(false);
  const [checkDisabled, setCheckDisabled] = useState(false);
  useMountEffect(() => {
    if (autoCheck) {
      handleCheck();
    }
  });
  const handleCheck = () => {
    setText('Checking for updates...');
    setChecking(true);
    //make api call
    //await POST body to http://makerhub.local:8990/connect
    async function checkUpdates() {
      // Default options are marked with *
      const response = await fetch(
        `http://makerhub.local:8080/api/v1/update/software/check`,
        {
          method: "POST",
          headers: {
            'Content-Type': 'application/json',
          }
        }
      );
      return response.json(); // parses JSON response into native JavaScript objects
    }
    checkUpdates().then(data => {
      console.log(data);
        //after api call
        if(data.update) {
          setChecking(false);
          //if updates present
          setText('Updates available!');
          setUpdates(true);
        } else {
          setChecking(false);
          setText('All up to date');
        }
     });
  };
  const handleDownload = () => {
    //await POST body to http://makerhub.local:8990/connect
    async function downloadUpdates() {
      // Default options are marked with *
      const response = await fetch(
        `http://makerhub.local:8080/api/v1/update/software`,
        {
          method: "POST",
          headers: {
            'Content-Type': 'application/json',
          }
        }
      );
      return response.json(); // parses JSON response into native JavaScript objects
    }
    setChecking(true);
    setText('Downloading, please wait...');
    setUpdates(false);
    //call download api
    downloadUpdates().then(data => {
      console.log(data);
        //after api call
        if(data.updated) {
          //after api call
          setChecking(false);
          //on success
          setText('Update successful, please reboot the printer.');
          setCheckDisabled(true);
        } else {
          //on failure
          setText('Something went wrong, please try again.');
          setUpdates(true);
          setCheckDisabled(true);
        }
     });
  };
  const handleSkip = () => {
    const userMode = getStorage('userMode');
    handleOnboarding(userMode);
  };
  return (
    <Grid
      container
      direction="column"
      alignItems="center"
      style={{ height: '100%' }}
    >
      {autoCheck && !checkDisabled && (
        <Button
          style={{ alignSelf: 'flex-end', marginTop: '15px' }}
          aria-label="skip"
          endIcon={<ArrowForward />}
          onClick={handleSkip}
        >
          Skip
        </Button>
      )}

      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        className={classes.updateContainer}
      >
        {checking && <CircularProgress color="secondary" />}
        {text.length > 0 && <Typography variant="h3">{text}</Typography>}
        {updates && (
          <Button
            variant="contained"
            color="secondary"
            onClick={handleDownload}
            style={{ marginTop: '24px' }}
          >
            Download Update
          </Button>
        )}
      </Grid>
      <Grid
        container
        justify="center"
        alignItems="center"
        className={classes.checker}
      >
        {!updates && !checking && (
          <Button
            variant="contained"
            disabled={checking || checkDisabled}
            onClick={handleCheck}
          >
            Check for updates
          </Button>
        )}
      </Grid>
    </Grid>
  );
};

export default Update;
