import React, { useEffect, useState } from 'react';
import {
  Backdrop,
  Button,
  CircularProgress,
  Drawer,
  Grid,
  IconButton,
  List,
  ListItemText,
  Modal,
  Paper,
  Select,
  Slider,
  Switch,
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TextField,
  Tooltip,
  Typography,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import moment from 'moment';
import { formatBytes } from '../../../Utility/helpers';

const useStyles = makeStyles((theme) => ({
  drawerContainer: {
    width: '100%',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
  },
  item: {
    height: '100px',
    padding: '0 22px',
  },
  modal: {
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    width: '75%',
    height: '60%',
  },
  modalContainer: {
    height: '100%',
    padding: '25px',
    '& h2': {
      fontSize: '2.5rem',
      marginTop: '10px',
    },
  },
  printButton: {
    width: '120px',
    height: '40px',
    alignSelf: 'center',
    marginTop: 'auto',
    marginBottom: '100px',
  },
  sliceDropdown: {
    border: '1px solid #ced4da',
    borderRadius: 4,
    width: '150px',
    padding: '0 10px',
  },
  slider: {
    maxWidth: '60px',
    marginLeft: '20px',
    '& input': {
      padding: '8px',
      textAlign: 'center',
    },
  },
  summary: {
    height: '56px',
    cursor: 'pointer',
    '&$selected, &$selected:hover': {
      backgroundColor: theme.palette.secondary.main,
    },
  },
  selected: {},
  hover: {},
  text: {
    fontSize: '1.5em',
    fontWeight: 'bold',
  },
}));

const FileManager = ({ drawerOpen, setDrawerOpen, fileLocation }) => {
  const classes = useStyles();
  const [selected, setSelected] = useState('');
  const [loading, setLoading] = useState(true);
  const [files, setFiles] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [filament, setFilament] = useState('');
  const [quality, setQaulity] = useState('good');
  const [infill, setInfill] = useState(20);
  const [supports, setSupports] = useState(true);
  const [showTooltip, setShowTooltip] = useState(false);

  useEffect(() => {
    if (drawerOpen) {
      setLoading(true);
      //make api call to fetch files
      //use fileLocation (usb or local) to determine which call to make
      //TODO:REMOVE TIMEOUT
      async function getFiles() {
        // Default options are marked with *
        let response = null;

        if (fileLocation === 'local') {
          response = await fetch(`http://makerhub.local:8080/api/v1/files`, {
            method: 'GET',
          });
        } else {
          await fetch('http://makerhub.local:8080/api/v1/usb/refresh', {
            method: 'POST',
          });
          response = await fetch(
            `http://makerhub.local:8080/api/v1/usb/files`,
            { method: 'GET' }
          );
        }
        return response.json(); // parses JSON response into native JavaScript objects
      }

      getFiles().then((data) => {
        console.log(data);
        setFiles(data.files);
        setLoading(false);
      });
    }
  }, [drawerOpen]);

  const handleSelect = () => {
    console.log(selected);
    if (fileLocation === 'local') {
      //call api to print file
      fetch(`http://makerhub.local:8080/api/v1/job/${selected}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
      });
    } else {
      //call api to copy and print file
      fetch(`http://makerhub.local:8080/api/v1/usb/copy/${selected}`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
      }).then((data) => {
        fetch(`http://makerhub.local:8080/api/v1/job/${selected}`, {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
        });
      });
    }

    setDrawerOpen(() => false);
    //call function or swap to status screen
    setSelected('');
    setShowTooltip(false);
    setShowModal(false);
  };
  const handleSlice = () => {
    //api logic to slice

    //call handleSelect once you have gcode file
    handleSelect();
  };

  return (
    <Drawer
      anchor="right"
      open={drawerOpen}
      onClose={() => setDrawerOpen(false)}
      classes={{
        paper: classes.drawerContainer,
      }}
    >
      <div className={classes.header}>
        <IconButton aria-label="back" onClick={() => setDrawerOpen(false)}>
          <ArrowBackIcon />
        </IconButton>
        <h2>{fileLocation.toUpperCase()}</h2>
      </div>
      {loading ? (
        <Backdrop className={classes.backdrop} open={true}>
          <CircularProgress color="inherit" />
        </Backdrop>
      ) : (
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Size</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {files &&
                files.map((file) => {
                  return (
                    <TableRow
                      key={file.display}
                      onClick={() => setSelected(file.display)}
                      selected={selected === file.display}
                      className={classes.summary}
                      classes={{
                        selected: classes.selected,
                        hover: classes.hover,
                      }}
                    >
                      <TableCell>{file.display}</TableCell>
                      <TableCell>
                        {moment(file.date).format('MM/DD/YYYY LTS')}
                      </TableCell>
                      <TableCell>{formatBytes(file.size)}</TableCell>
                    </TableRow>
                  );
                })}
            </TableBody>
          </Table>
        </TableContainer>
      )}
      {!selected.endsWith('.stl') ? (
        <Button
          variant="contained"
          disabled={selected.length <= 0}
          onClick={() => handleSelect()}
          className={classes.printButton}
        >
          Print
        </Button>
      ) : (
        <Button
          variant="contained"
          disabled={selected.length <= 0}
          onClick={() => setShowModal(true)}
          className={classes.printButton}
        >
          Slice
        </Button>
      )}
      <Modal open={showModal} onClose={() => setShowModal(false)}>
        <Paper className={classes.modal}>
          <Grid container direction="column" className={classes.modalContainer}>
            <Grid item style={{ alignSelf: 'center' }}>
              <Typography variant="h2">Slice Menu</Typography>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Filament Type" />
                <Tooltip
                  PopperProps={{
                    disablePortal: true,
                  }}
                  onClose={() => setShowTooltip(false)}
                  open={showTooltip}
                  disableFocusListener
                  disableHoverListener
                  title="Must select filament type"
                  onOpen={() => setShowTooltip(() => false)}
                >
                  <Select
                    native
                    value={filament}
                    onChange={(e) => {
                      if (e.target && e.target.value) {
                        setFilament(e.target.value);
                      }
                    }}
                    MenuProps={{
                      anchorOrigin: {
                        vertical: 'bottom',
                        horizontal: 'left',
                      },
                      getContentAnchorEl: null,
                    }}
                    className={classes.sliceDropdown}
                  >
                    <option value="" />
                    <option value="pla">PLA</option>
                    <option value="petg">PETG</option>
                    <option value="tpu">TPU</option>
                  </Select>
                </Tooltip>
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Print Quality" />
                <Select
                  native
                  value={quality}
                  onChange={(e) => {
                    if (e.target && e.target.value) {
                      setQaulity(e.target.value);
                    }
                  }}
                  MenuProps={{
                    anchorOrigin: {
                      vertical: 'bottom',
                      horizontal: 'left',
                    },
                    getContentAnchorEl: null,
                  }}
                  className={classes.sliceDropdown}
                >
                  <option value="good">Fastest Print</option>
                  <option value="better">Normal Quality</option>
                  <option value="best">Best Quality</option>
                </Select>
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Infill" />
                <Grid container item>
                  <Slider
                    defaultValue={infill}
                    color="secondary"
                    onChange={(e, newVal) => setInfill(newVal)}
                    getAriaValueText={() => `${infill}%`}
                    valueLabelDisplay="auto"
                    step={5}
                    min={0}
                    max={100}
                    style={{ maxWidth: '200px' }}
                  />
                  <TextField
                    value={`${infill}%`}
                    InputProps={{
                      readOnly: true,
                    }}
                    variant="outlined"
                    className={classes.slider}
                  />
                </Grid>
              </List>
            </Grid>
            <Grid item>
              <List className={classes.root}>
                <ListItemText primary="Supports" />
                <Switch
                  checked={supports}
                  onChange={(e) => setSupports(e.target.checked)}
                  name="supports"
                />
              </List>
            </Grid>
            <Grid
              item
              container
              justify="space-around"
              alignItems="center"
              style={{ flex: 1 }}
            >
              <Button
                variant="contained"
                color="secondary"
                onClick={() => setShowModal(false)}
              >
                Cancel
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={() => {
                  if (filament.length > 0) {
                    handleSlice();
                  } else {
                    setShowTooltip(true);
                  }
                }}
              >
                Print
              </Button>
            </Grid>
          </Grid>
        </Paper>
      </Modal>
    </Drawer>
  );
};

export default FileManager;
