import React, { useState } from 'react';
import {
  Button,
  Box,
  Grid,
  Slider,
  Typography
} from '@material-ui/core';
import './Heating.css';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  drawerContainer: {
    width: '100%',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
  },
  item: {
    height: '100px',
    padding: '0 22px',
  },
  modal: {
    position: 'absolute',
    left: '50%',
    top: '50%',
    transform: 'translate(-50%, -50%)',
    width: '75%',
    height: '60%',
  },
  modalContainer: {
    height: '100%',
    padding: '25px',
    '& h2': {
      fontSize: '2.5rem',
      marginTop: '10px',
    },
  },
  printButton: {
    width: '120px',
    height: '40px',
    alignSelf: 'center',
    marginTop: 'auto',
    marginBottom: '100px',
  },
  sliceDropdown: {
    border: '1px solid #ced4da',
    borderRadius: 4,
    width: '150px',
    padding: '0 10px',
  },
  slider: {
    maxWidth: '60px',
    marginLeft: '20px',
    '& input': {
      padding: '8px',
      textAlign: 'center',
    },
  },
  summary: {
    height: '56px',
    cursor: 'pointer',
    '&$selected, &$selected:hover': {
      backgroundColor: theme.palette.secondary.main,
    },
  },
  selected: {},
  hover: {},
  text: {
    fontSize: '1.5em',
    fontWeight: 'bold',
  },
  gauges: {
    display: 'flex',
  },
  nozzleSlider: {
    display: 'flex',
    flexDirection: 'row',
    width: 250
  },
  tempOutputs: {
    display: 'flex',
    flexDirection: 'column'
  }
}));

const Heating = ({ bed, extruder }) => {
  let sendGcode = function (gcode) {
    fetch(`http://makerhub.local:8080/api/v1/send`, {
      method: 'POST',
      mode: 'no-cors',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify({
        gcode: gcode,
      }),
    });
  };

  const [nozzleTemp, setNozzleTemp] = useState(0);
  const [bedTemp, setBedTemp] = useState(0);
  const classes = useStyles();

  return (
   
      <Box style={{textAlign: 'center'}}>
      <Typography variant="h4">Heating</Typography>      
        <Grid container justify="center" spacing={6} style={{ margin: 0 }}>
        <div className="control-heating-container">
          <h4>Preheat</h4>
          <div style={{ display: 'flex', flexDirection: 'column' }}>
            <Button
              color="primary"
              variant="contained"
              style={{margin: 3}}
              onClick={() => {
                sendGcode('M104 S220\nM140 S50');
              }}
            >
              PLA
            </Button>
            <Button
              color="primary"
              variant="contained"
              style={{margin: 3}}
              onClick={() => {
                sendGcode('M104 S245\nM140 S70');
              }}
            >
              PETG
            </Button>
            <Button
              color="primary"
              variant="contained"
              style={{margin: 3}}
              onClick={() => {
                sendGcode('M104 S230\nM140 S50');
              }}
            >
              TPU
            </Button>
            <Button
              color="primary"
              variant="contained"
              style={{margin: 3}}
              onClick={() => {
                sendGcode('M104 S0\nM140 S0');
              }}
            >
              Cooldown
            </Button>
          </div>
          </div>
        
        <div className="monitor-heating-container">
          <div className="gauges">
            <div className={classes.nozzle}>
              <span>Nozzle</span>
              <div className={classes.nozzleSlider}>
              <Slider
                    defaultValue={0}
                    color="secondary"
                    onChangeCommitted={(e, newVal) => {setNozzleTemp(newVal); sendGcode(`M104 S${nozzleTemp}\n`);}}
                    getAriaValueText={() => `${nozzleTemp}C`}
                    valueLabelDisplay="auto"
                    step={5}
                    min={0}
                    max={260}
                    style={{ maxWidth: '200px' }}
                  />
                  <div className={classes.tempOutputs}>
                  <span>{`${extruder}C`}</span>
                  <span>{`${nozzleTemp}C`}</span>
                  </div>
                </div>
            </div>
            <div className="bed">
              <span>Bed</span>
              <div className={classes.nozzleSlider}>
              <Slider
                    defaultValue={0}
                    color="secondary"
                    onChangeCommitted={(e, newVal) => {setBedTemp(newVal); sendGcode(`M140 S${bedTemp}\n`);}}
                    getAriaValueText={() => `${bedTemp}C`}
                    valueLabelDisplay="auto"
                    step={5}
                    min={0}
                    max={100}
                    style={{ maxWidth: '200px' }}
                  />
                  <div className={classes.tempOutputs}>
                  <span>{`${bed}C`}</span>
                  <span>{`${bedTemp}C`}</span>
                  </div>
              </div>
            </div>
          </div>
          <Grid container item>
                  
                  
            </Grid>
        </div>
        </Grid>
      </Box>
  
  );
};

export default Heating;
